body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
}

nav {
  height: 70px;
  display: flex;
  padding: 20px 36px;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.1);
}

.gray {
  color: #918E9B;
}

.airRed {
  color: #FF5A60;
  height: 12px;
}

.bold {
  font-weight: bold;
}

.navbar--logo {
  max_width: 100px;
}

.hero {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.hero--photo {
  max-width: 400px;
  align-self: center;
}

.hero--header {
  margin-bottom: 20px;
}

.hero--text {
  margin-top: 0;
}

.card {
  position: relative;
  width: 180px;
  font-size: 14px;
  margin: 20px;
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
}

.card--photo {
  width: 100%;
  border-radius: 16px;
  margin-bottom: 10px;
  height: 250px;
  object-fit: cover;
}

.card-stats {
  display: flex;
  align-items: center;
}

.cards {
  display: flex;
  flex-direction: row;
  overflow: scroll;
}

.badge {
  position: absolute;
  top: 8px;
  left: 8px;
  padding: 5px;
  margin: 0;
  background-color: white;
  border-radius: 10px;
  font-weight: 600;
  color: #c0c0c0;
}

